async function getById(toyId) {
    try {
        const toy = await httpService.get(`toy/${toyId}`)
        // const { data } = await axios.get(`http://localhost:3030/api/toy/${toyId}`)
        return toy
    } catch (err) {
        console.log(err.response.status)
    }
}

async function remove(toyId) {
    try {
        return httpService.delete(`toy/${toyId}`)

    } catch (err) {
        console.log(err, 'err')
        throw new Error(err)
    }
}

async function save(toy) {
    if (toy._id) {
        toy.createdAt = Date.now()
        try {
            const toyToSave = await httpService.put(`toy/${toy._id}`, toy)
            return toyToSave
        } catch (err) {
            console.log(err, 'err')
            throw new Error(err)
        }
    }
    else {
        try {
            const toyToAdd = await httpService.post(`toy`, toy)
            return toyToAdd
            // return data
        } catch (err) {
            console.log(err, 'err')
        }
        //     toy.owner = userService.getLoggedinUser()
        // toy._id = utilService.makeId()
        // console.log('toy:', toy);
    }
}

