import React from 'react'
import { connect } from 'react-redux'
import { toyService } from '../services/toy.service.js'
import { ToyReviews } from '../cmps/toy-reviews.jsx'
import doll from '../assets/img/toy.jpeg'
class _ToyDetails extends React.Component {
    state = {
        toy: null
    }

    componentDidMount() {
        const { toyId } = this.props.match.params;
        console.log('toyId:', toyId);
        toyService.getById(toyId)
            .then(toy => {
                console.log('toy:', toy);
                // this.lastSavedState = { ...toy };
                return toy;
            })
            .then(toy => this.setState({ toy }));
    }


    onGoBack = () => {
        this.props.history.push('/toy');
    }

    render() {
        const { toy } = this.state;
        if (!toy) return 'No such toy'
        return (
            <section>
                <button className="go-back-btn" onClick={this.onGoBack} title="go back">Back</button>
                <section className='details-container'>
                    <h2>{toy.name}</h2>
                    {toy.labels.map((label, idx) => <h3 key={idx}>{label}</h3>)}
                    <p>Price: {toy.price}$</p>
                    {toy.inStock ? <p className='instock'>In Stock!</p> : <p className='outstock'>Out of Stock!</p>}
                    <div className="toy-edit-form">
                        <img className='toy-img ' alt="toy" src={doll} />
                    </div>
                    <ToyReviews toy={toy} />
                </section>
            </section>
        )
    }

}





function mapStateToProps({ toyModule }) {
    return {
        toy: toyModule.toy
    };
}
export const ToyDetails = connect(mapStateToProps)(_ToyDetails)





