import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Link } from 'react-router-dom';
import { Grid } from '@mui/material';
import { connect } from 'react-redux';

// import { userService } from '../services/user.service';
// import {UserACTIO}
import { login, signup, logout } from '../store/user.action.js'
const theme = createTheme();
export class _LoginSignup extends React.Component {
    state = {
        // credentials: userService.emptyUser(),
        isSignup: false
    }

    toggleSignup = () => {
        this.setState({ isSignup: !this.state.isSignup })
    }


    onLogin = async (ev) => {
        try {
            ev.preventDefault();
            const data = new FormData(ev.currentTarget);
            const userInfo = {
                username: data.get('username'),
                password: data.get('password'),
            }
            await this.props.login(userInfo)
            // this.setState({
            //     credentials: userInfo,
            // });
            console.log('this.state:', this.state);
            // const history = useHistory();
            console.log('this.props.history:', this.props.history);


            this.props.goToToys()
        } catch (err) {
            console.error(err)
        }

        // this.clearState()
    }

    onLogout = () => {
        this.props.logout()

        this.props.goToToys()

    }

    onSignup = (ev) => {
        ev.preventDefault();
        const data = new FormData(ev.currentTarget);
        const userInfo = {
            fullname: data.get('fullname'),
            username: data.get('username'),
            password: data.get('password'),
            isAdmin: false
        }
        console.log('userInfo:', userInfo);
        this.props.signup(userInfo)

        this.props.goToToys()

    }



    render() {
        const { isSignup } = this.state;
        const { user } = this.props
        return (
            <ThemeProvider theme={theme}>
                <Container component="main" maxWidth="xs">
                    <CssBaseline />

                    <Box
                        style={{
                            marginTop: 8,
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                        }}
                    >
                        {!user && <Avatar style={{ margin: 1, backgroundColor: '#9c27b0' }}>
                            <LockOutlinedIcon />
                        </Avatar>}
                        {!user && isSignup && <React.Fragment>
                            <Typography component="h1" variant="h5">
                                Sign up
                            </Typography>
                            <Box component="form" onSubmit={this.onSignup} noValidate style={{ marginTop: 1 }}>
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    id="fullname"
                                    label="full Name"
                                    name="fullname"
                                    // autoComplete="email"
                                    autoFocus
                                />
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    id="username"
                                    label="user Name"
                                    name="username"
                                />
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    name="password"
                                    label="Password"
                                    type="password"
                                    id="password"
                                    autoComplete="current-password"
                                />
                                {/* <FormControlLabel
                            control={<Checkbox value="remember" color="primary" />}
                            label="Remember me"
                        /> */}
                                <Button
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    style={{ marginTop: 3, marginBottom: 2 }}
                                >
                                    Sign Up
                                </Button>
                            </Box>
                        </React.Fragment>}
                        {!user && !isSignup && <React.Fragment>
                            <Typography component="h1" variant="h5">
                                Log in
                            </Typography>
                            <Box component="form" onSubmit={this.onLogin} noValidate style={{ marginTop: 1 }}>
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    id="username"
                                    label="user Name"
                                    name="username"
                                    autoFocus
                                />
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    name="password"
                                    label="Password"
                                    type="password"
                                    id="password"
                                    autoComplete="current-password"
                                />
                                {/* <FormControlLabel
                            control={<Checkbox value="remember" color="primary" />}
                            label="Remember me"
                        /> */}
                                <Button
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    style={{ marginTop: 3, marginBottom: 2 }}
                                >
                                    Log In
                                </Button>
                            </Box>
                        </React.Fragment>}
                        {user && <Button

                            fullWidth
                            variant="contained"
                            style={{ marginTop: 3, marginBottom: 2 }}
                            onClick={this.onLogout}
                        >
                            Log Out
                        </Button>}                       <Grid container>
                            <Grid item>
                                <Link href="#" variant="body2" onClick={this.toggleSignup}>
                                    {!user && !isSignup ? "Don't have an account? Sign Up" : !user && "have an account? Log In"}
                                </Link>
                            </Grid>
                        </Grid>
                    </Box>
                </Container>
            </ThemeProvider>
        );
    }
}

function mapStateToProps({ userModule }) {
    return { user: userModule.user }
}

const mapDispatchToProps = {
    login,
    signup,
    logout
}

export const LoginSignup = connect(mapStateToProps, mapDispatchToProps)(_LoginSignup)