import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { LoginSignup } from '../cmps/sign-in.jsx'
// import logo from '../assets/img/logo.png'
// import { changeCount, changeCountLater, onDecrease } from '../store/car.action.js'

class _HomePage extends React.Component {
    state = {
    }
    goToToys = () => {
        this.props.history.push('/toy')
    }

    render() {
        const { user } = this.props
        // const { count, user, changeCount, changeCountLater } = this.props
        return (
            <section>
                {user ? <h1 className='hello-user'>Hello, {user.username}!</h1> : <h1 className='hello-user'>Hello, stranger!</h1>}
                <Link to="/toy"><button>To the toys!</button></Link>
                {/* <Link to="/chart">To the toys chart!</Link> */}
                <LoginSignup goToToys={this.goToToys} />
            </section>

            //     {user && <h3>Welcome {user.fullname}</h3>}
            // </section >
        )
    }
}



function mapStateToProps({ userModule }) {
    return {
        user: userModule.user,
    }
}
const mapDispatchToProps = {
    // changeCount,
    // changeCountLater,
    // onDecrease
}


export const HomePage = connect(mapStateToProps, mapDispatchToProps)(_HomePage)