
import React from 'react'
import Select from 'react-select'

const options = [

    { value: 'On wheels', label: 'On wheels' },
    { value: 'Box game', label: 'Box game' },
    { value: 'Baby', label: 'Baby' },
    { value: 'Art', label: 'Art' },
    { value: 'Doll', label: 'Doll' },
    { value: 'Puzzle', label: 'Puzzle' },
    { value: 'Outdoor', label: 'Outdoor' }
]




export class ToyFilter extends React.Component {
    state = {
        currFilterBy: {
            inStock: "ALL",
            labels: [],
            name: "",
        },
    };

    handleChange = ({ target }) => {
        console.log(target.value);
        const field = target.name;
        var value;
        if (target.value === "ALL") value = "ALL";
        else if (target.value === "INSTOCK") value = target.value === "INSTOCK";
        else value = !target.value === "OUTSTOCK";
        this.setState(
            (prevState) => ({
                currFilterBy: { ...prevState.currFilterBy, [field]: value },
            }))
    };
    // handleLabelChange = ({ target }) => {
    //     console.log(target.value);
    //     const field = target.name;
    //     var value;
    //     if (target.value === "ALL") value = "ALL";
    //     else value = target.value
    //     this.setState(
    //         (prevState) => ({
    //             currFilterBy: { ...prevState.currFilterBy, [field]: value },
    //         }))
    // };
    handleLabelChange = (options) => {
        console.log('options:', options);

        this.setState(
            (prevState) => ({
                currFilterBy: { ...prevState.currFilterBy, labels: [...options.map(option => option.value)] },
            })

        )

    };

    handleChangeTxt = ({ target }) => {
        const field = target.name;
        var value = target.value;
        this.setState(
            (prevState) => ({
                currFilterBy: { ...prevState.currFilterBy, [field]: value },
            }))
        this.props.onSetFilter(this.state.currFilterBy)
    };

    onSubmitFilter = (ev) => {
        ev.preventDefault();
        const { currFilterBy } = this.state;
        this.props.onSetFilter(currFilterBy);
    };
    // const MyComponent = () => (
    //     <Select options={options} />
    //   )

    render() {
        return (
            <section className="filter-toy-container">
                <div className='toy-filter-container'>
                    <h1>Filter toy</h1>
                    <form className="toy-filter-form" onSubmit={this.onSubmitFilter}>
                        <label htmlFor="name">Filter by toy's name:</label>
                        <input
                            type="text"
                            name="name"
                            placeholder="Serch by text"
                            onChange={this.handleChangeTxt}
                        ></input>
                        <label htmlFor="inStock">Filter by What's in stock:</label>
                        <select onChange={this.handleChange} placeholder='InStock?' name="inStock">
                            <option value="ALL">All</option>
                            <option value="INSTOCK">In Stock</option>
                            <option value="OUTSTOCK">Out Stock</option>
                        </select>
                        <label htmlFor="labels">Filter by label:</label>

                        <Select options={options} isMulti onChange={this.handleLabelChange} placeholder='filter by label' name="labels" />

                        {/* <select onChange={this.handleLabelChange} placeholder='filter by label' name="labels">
                            <option value="ALL">All</option>
                            <option value="On wheels">On wheels</option>
                            <option value="Box game">Box game</option>
                            <option value="Art">Art</option>
                            <option value="Baby">Baby</option>
                            <option value="Doll">Doll</option>
                            <option value="Puzzle">Puzzle</option>
                            <option value="Outdoor">Outdoor</option>
                        </select> */}




                        <button>Filter</button>
                    </form>
                </div>
            </section>
        );
    }
}

