import { connect } from 'react-redux'
import React from 'react'
import Select from 'react-select'
import { setSort } from '../store/toy.action'
const options = [
    { value: 'Price', label: 'Price' },
    { value: 'Name', label: 'Name' }
]



class _ToySort extends React.Component {
    state = {
        currSortBy: {
            field: null,
            order: "asc"
        }

    };
    // onSortByName = () => {
    //     console.log('sorting:');
    //     this.setState({ currSortBy: "name" })
    //     this.props.onSetSort(this.state.currSortBy)

    // }
    // onSortByPrice = () => {
    //     console.log('sorting by price:');
    //     this.setState({ currSortBy: "price" })
    //     this.props.onSetSort(this.state.currSortBy)

    // }

    handleSortBy = (changedSortBy) => {
        console.log('sortBy:', changedSortBy);
        const { value } = changedSortBy
        console.log('value:', value);

        this.setState((prevState) => ({
            currSortBy: {
                ...prevState.currSortBy,
                field: value,
                // order: prevState.currSortBy.field === 'asc' ? 'desc' : 'asc'
            },
        }))
        this.props.setSort(this.state.currSortBy)
    }

    render() {

        const { toys } = this.props
        console.log('toys:', toys);
        return (
            <section>
                <h1>sort by</h1>
                <Select options={options} onChange={this.handleSortBy} placeholder='sort by' name="sortby" />


            </section>
        )
    }
}

function mapStateToProps({ toyModule }) {
    return {
        toys: toyModule.toys
    };
}


const mapDispatchToProps = {
    setSort
};


export const ToySort = connect(mapStateToProps, mapDispatchToProps)(_ToySort)

