import { Link, NavLink } from 'react-router-dom'
// import logo from '../assets/img/'
export function AppHeader() {
    return (
        <header className="app-header justify-between">
            <div className="header-content flex">
                <Link className='clean-link' to="/">
                    <h1 className='logo'>Miss Toy</h1>
                    {/* <img alt='logo' src={logo} /> */}
                </Link>
                <nav className="header-nav">
                    <NavLink className="clean-link header-link" to="/">
                        Home
                    </NavLink>
                    <NavLink className="clean-link header-link" to="/toy">
                        Toys
                    </NavLink>
                    {/* <NavLink className="clean-link header-link" to="/about">
                        About
                    </NavLink> */}
                    <NavLink to="/chart" className="clean-link header-link">Stats</NavLink>
                </nav>
            </div>
        </header>
    )
}