
import { toyService } from '../services/toy.service.js'
// import { showSuccessMsg, showErrorMsg } from '../services/event-bus.service.js'


export function loadToys() {
    return async (dispatch) => {
        try {
            const toys = await toyService.query();
            console.log('toys:', toys);
            const action = { type: "SET_TOYS", toys: toys };
            dispatch(action);
        } catch (err) {
            console.error(err)
        }
    }
}

export function removeToy(toyId) {
    return async (dispatch) => {
        try {
            await toyService.remove(toyId)
            console.log("Deleted Succesfully!");
            dispatch({ type: "REMOVE_TOY", toyId: toyId });
            // dispatch({ type: 'SET_MSG', msg: { txt: 'Toy Removed', type: 'success' } })
        } catch (err) {
            console.error(err);
        };
    }
}

export function loadUpdatedToys(currFilterBy) {
    return async (dispatch) => {
        try {
            const toys = await toyService.query(currFilterBy)
            const action = { type: "SET_TOYS", toys };
            return dispatch(action);
        } catch (err) {
            console.error('couldnt load edit toys', err)
        }
    }
}

export function toggleToy(toy) {
    return async (dispatch) => {
        try {
            const toyToSave = { ...toy, isDone: !toy.isDone };
            const savedToy = await toyService.save(toyToSave)
            dispatch({ type: "UPDATE_TOY", toy: savedToy });
            // dispatch({ type: 'SET_MSG', msg: { txt: 'Toy Updated', type: 'success' } })
        } catch (err) {
            // dispatch({ type: 'SET_MSG', msg: { txt: 'cannot update toy', type: 'danger' } })
            console.error(err)
        };
    }
}
export function setFilter(currFilterBy) {
    return (dispatch) => {
        dispatch({ type: "SET_FILTER", currFilterBy: currFilterBy });
    }
}
export function setSort(currSortBy) {
    console.log('sortByONACTION', currSortBy)
    return (dispatch) => {
        dispatch({ type: "SET_SORT", currSortBy: currSortBy });
    }
}
export function addToy(toy) {
    console.log('toy:', toy);
    return async (dispatch) => {
        try {
            const savedToy = await toyService.save({ ...toy })
            console.log('savedToy:', savedToy);
            const action = { type: "ADD_TOY", toy: savedToy };
            dispatch(action);
            // dispatch({ type: 'SET_MSG', msg: { txt: 'Toy added', type: 'success' } })
        } catch (err) {
            console.error(err)
        };
    }
}

export function closeUserMsg() {
    return (dispatch) => {
        dispatch({ type: 'SET_MSG', msg: null })
    }
}