import React from 'react'
// import { connect } from 'react-redux'
import { Link } from 'react-router-dom';

export function ToyPreview({ toy, onRemoveToy, onToggleToy, user }) {
    // console.log('toy:', toy);

    return (

        <div
            className="toy-container"
        // style={user && user.username === toy.user ? { backgroundColor: `${user.bgColor}`, color: `${user.color}` } : { backgroundColor: "white", color: "black" }}
        >
            <h2>{toy.name}</h2>
            {toy.labels.map((label, idx) => <h3 key={idx}>{label}</h3>)}
            <p>Price: {toy.price}$</p>
            {toy.inStock ? <p className='instock'>In Stock!</p> : <p className='outstock'>Out of Stock!</p>}
            <div className='edit-btns-div'>
                {user && user.isAdmin && <React.Fragment>
                    <button onClick={() => onRemoveToy(toy._id)}>DELETE</button>
                    <Link to={`/edit/${toy._id}`}><button>EDIT</button></Link>
                </React.Fragment>}
                <Link to={`/details/${toy._id}`}><button>MORE DETAILS</button></Link>
            </div>
        </div>
    );
}

