import { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { loadReviews, addReview, removeReview } from '../store/review.action.js'
import { loadUsers } from '../store/user.action.js'

function _ToyReviews({ toy, reviews, users, loggedInUser, loadReviews, loadUsers, addReview, removeReview }) {
    // console.log('toy:', toy);
    console.log('toy.reviews:', toy.reviews);
    console.log('reviews:', reviews);

    const [reviewToEdit, setReviewToEdit] = useState({ txt: '', toyId: toy._id })

    useEffect(() => {
        loadReviews(toy._id)
        loadUsers()
        setReviewToEdit({ txt: '', toyId: toy._id })
    }, [])


    function handleChange(ev) {
        const { name, value } = ev.target;
        setReviewToEdit({ ...reviewToEdit, [name]: value });
    }



    const onAddReview = async ev => {
        ev.preventDefault()
        // if (!reviewToEdit.txt || !reviewToEdit.aboutToyId) return alert('All fields are required')
        if (!reviewToEdit.txt) alert('All fields are required')
        await addReview(reviewToEdit)
        setReviewToEdit({ txt: '', toyId: toy._id })
    }
    return (
        <section>
            {loggedInUser && <form onSubmit={onAddReview}>
                <textarea name='txt' onChange={handleChange} value={reviewToEdit.txt}>
                </textarea>
                <div>
                    <button>Submit</button>
                </div>
            </form>}
            {reviews && <ul className="review-list  reviews-container">
                {reviews.map(review => (

                    <li className="review clean-link" key={review._id}><span className="reviewer-name">{loggedInUser.username}:</span> "{review.txt}"</li>
                ))}
            </ul>}
            {/* <section className="reviews-container">
                <h1>Customer's reviews for "{toy.name}"</h1>
                <ul>
                    {toy.reviews.map((review, idx) => {
                        return <li className="review clean-link" key={idx}><span className="reviewer-name">{review.username}:</span> "{review.review}"</li>
                    })}
                </ul>
            </section> */}
        </section>

    )
}

const mapStateToProps = state => {
    return {
        reviews: state.reviewModule.reviews,
        users: state.userModule.users,
        loggedInUser: state.userModule.user
    }
}
const mapDispatchToProps = {
    loadReviews,
    loadUsers,
    addReview,
    removeReview
}

export const ToyReviews = connect(mapStateToProps, mapDispatchToProps)(_ToyReviews)