import React from 'react'
import { connect } from 'react-redux'
import Select from 'react-select'
// import { Formik, Form, Field, ErrorMessage } from 'formik';

import { addToy } from '../store/toy.action.js'


const options = [
    { value: 'On wheels', label: 'On wheels' },
    { value: 'Box game', label: 'Box game' },
    { value: 'Baby', label: 'Baby' },
    { value: 'Art', label: 'Art' },
    { value: 'Doll', label: 'Doll' },
    { value: 'Puzzle', label: 'Puzzle' },
    { value: 'Outdoor', label: 'Outdoor' }
]



// import { showSuccessMsg, showErrorMsg } from '../services/event-bus.service.js'

class _ToyAdd extends React.Component {
    // console.log('this.props.user.color:', this.props.user.color);

    state = {
        toy: {
            name: "",
            price: 0,
            labels: [],
            createdAt: Date.now(),
            inStock: true
        }
    }

    onAddToy = (ev) => {
        ev.preventDefault();
        console.log('ev:', ev);

        const { toy } = this.state;
        console.log("toy in toy add", toy);
        this.props.addToy(toy)

    };

    onHandleLablesChange = (options) => {
        console.log("options", options);
        this.setState((prevState) => ({
            toy: { ...prevState.toy, labels: [...options.map(option => option.value)] },
        }));
    };

    onHandleChange = ({ target }) => {
        const field = target.name;
        const value = target.value;
        console.log('field:', field);
        console.log('value:', value);
        this.setState((prevState) => ({
            toy: { ...prevState.toy, [field]: value },
        }));
    };

    render() {
        return (
            <section className="toy-app">
                <div className="add-toy-container">
                    <h1>Add new toy</h1>
                    <form className="add-toy-form" onSubmit={this.onAddToy}>
                        {/* <div> */}
                        <label htmlFor="name">Enter new toy:</label>
                        <input
                            placeholder="Enter Toy"
                            value={this.state.toy.name}
                            name="name"
                            onChange={this.onHandleChange}
                        ></input>
                        {/* </div> */}
                        {/* <div> */}
                        <label htmlFor="price">Enter price:</label>
                        <input
                            placeholder="Enter price"
                            type="number"
                            value={this.state.toy.price}
                            name="price"
                            onChange={this.onHandleChange}
                        ></input>
                        {/* </div> */}
                        <label htmlFor="labels">Add labels:</label>

                        <Select options={options} isMulti onChange={this.onHandleLablesChange} placeholder='Choose labels' name="labels" />


                        {/* <label>
                            Choose a label from this list:
                            <input onChange={this.onHandleChange} list="allLabels" name="labels" placeholder="Choose label" />
                        </label>
                        <datalist id="allLabels">
                            <option value="On wheels" />
                            <option value="Box game" />
                            <option value="Art" />
                            <option value="Baby" />
                            <option value="Doll" />
                            <option value="Puzzle" />
                            <option value="Outdoor" />
                        </datalist> */}

                        <div className="add-toy-btn-container">
                            <button className="add-toy-btn">Add Toy</button>
                        </div>
                    </form>
                </div>
            </section>
        );
    }
}
function mapStateToProps({ toyModule, userModule }) {
    return {
        toy: toyModule.toy,
        user: userModule.user,
    };
}

const mapDispatchToProps = {
    addToy
}

export const ToyAdd = connect(mapStateToProps, mapDispatchToProps)(_ToyAdd);
