import React from 'react'
import { connect } from 'react-redux'
// import { Link } from 'react-router-dom'
import { loadToys, removeToy, setFilter, setSort, loadUpdatedToys } from '../store/toy.action.js'
import { login, signup, logout } from '../store/user.action.js'
import { ToyAdd } from '../cmps/toy-add'
import { ToyList } from '../cmps/toy-list'
import { ToyFilter } from '../cmps/toy-filter'
import { ToySort } from '../cmps/toy-sort.jsx'
// import { removeToy, toggleToy, loadToys, loadUpdatedToys, setFilter } from '../store/toy.action.js'
// import { toyService } from '../services/toy.service.js'
// import { showSuccessMsg } from '../services/event-bus.service.js'

class _ToyApp extends React.Component {
    state = {}
    componentDidMount() {
        this.props.loadToys()
    }


    componentDidUpdate(prevProps, prevState) {
        const { currFilterBy, currSortBy } = this.props;
        if (prevProps.currFilterBy !== currFilterBy || prevProps.currSortBy !== currSortBy) {
            this.props.loadUpdatedToys(currFilterBy)
        }
    }

    onRemoveToy = (toyId) => {
        this.props.removeToy(toyId)
    }

    onSetFilter = (currFilterBy) => {
        this.props.setFilter(currFilterBy)
    };

    onSetSort = (currSortBy) => {
        this.props.setSort(currSortBy)
    };


    // onAddToy = () => {
    //     const toy = toyService.getEmptyToy();
    //     this.props.addToy(toy)
    // }
    // onEditToy = (toy) => {
    //     const price = +prompt('New price?');
    //     const toyToSave = { ...toy, price }
    //     this.props.updateToy(toyToSave)
    // }
    // addToToys = (toy) => {
    //     this.props.addToToys(toy)
    //     // showSuccessMsg('ADDED')

    // }
    render() {
        const { toys, user } = this.props
        console.log('user:', user);

        return (
            <section className="toy-app main-container">
                {user ? <h1 className='hello-user'>Hello, {user.username}!</h1> : <h1 className='hello-user'>Hello, stranger!</h1>}
                <div className='add-filter flex justify-space'>
                    <ToyFilter onSetFilter={this.onSetFilter} />
                    <ToySort onSetSort={this.onSetSort} />
                    {user && user.isAdmin && <ToyAdd />}
                </div>
                {/* <Link className='chart-link' to="/chart">Go to toys chart</Link> */}
                <section className="toys-list-container">
                    <ToyList
                        user={user}
                        toys={toys}
                        onRemoveToy={this.onRemoveToy}
                        onToggleToy={this.onToggleToy}
                    />
                </section>
            </section>
        );
    }
}

function mapStateToProps({ toyModule, userModule }) {
    return {
        toys: toyModule.toys,
        currFilterBy: toyModule.currFilterBy,
        currSortBy: toyModule.currSortBy,
        user: userModule.user,
    };
}


const mapDispatchToProps = {
    loadToys,
    removeToy,
    // toggleToy,
    loadUpdatedToys,
    setFilter,
    setSort,
    login,
    signup,
    logout
};


export const ToyApp = connect(mapStateToProps, mapDispatchToProps)(_ToyApp)


