
const initialState = {
    currFilterBy: "",
    currSortBy: "",
    toy: null,
    toys: [],
    // TOY: REMOVE USER
    // user: null,
    msg: null
};

export function toyReducer(state = initialState, action) {
    let newState = state;
    switch (action.type) {
        case "SET_TOYS":
            newState = {
                ...state,
                toys: [...action.toys],
            };
            break;
        case "SET_FILTER":
            newState = {
                ...state,
                currFilterBy: {
                    ...action.currFilterBy,
                },
            };
            break;
        case "SET_SORT":
            newState = {
                ...state,
                currSortBy: {
                    ...action.currSortBy,
                },
            };
            break;
        case "ADD_TOY":
            newState = {
                ...state,
                toys: [...state.toys, action.toy],
            };
            break;
        case "REMOVE_TOY":
            newState = {
                ...state,
                toys: state.toys.filter((toy) => toy._id !== action.toyId),
            };
            break;
        case "UPDATE_TOY":
            newState = {
                ...state,
                toys: state.toys.map((currToy) => {
                    return currToy._id === action.toy._id ? action.toy : currToy;
                }),
            };
            break;

        case "SET_MSG":
            newState = { ...state, msg: action.msg };
            break;
        default:
    }
    return newState;
}