import React from 'react'
import { connect } from 'react-redux'
// import swal from 'sweetalert'
import { toyService } from '../services/toy.service.js';


class _ToyEdit extends React.Component {
    state = {
        toy: null
    }

    inputRef = React.createRef();
    lastSavedState;

    componentDidMount() {
        // console.log(this.inputRef)
        // this.inputRef.current.focus();
        const { toyId } = this.props.match.params;
        toyService.getById(toyId)
            .then(toy => {
                this.lastSavedState = { ...toy };
                return toy;
            })
            .then(toy => this.setState({ toy }));
    }


    handleChange = ({ target }) => {
        let { name, value } = target;
        console.log('name:', name);
        console.log('value:', value);
        if (value === 'Yes') {
            value = true
        }
        if (value === 'No') {
            value = false
        }

        this.setState(prevState => ({ toy: { ...prevState.toy, [name]: value } }));
    }

    onSave = (ev) => {
        ev.preventDefault();
        toyService.save(this.state.toy)
            .then(() => this.props.history.push('/toy'))
    }

    onGoBack = () => {
        this.props.history.push('/toy');
    }

    render() {
        const { toy } = this.state;
        if (!toy) return 'No such toy'
        return (
            <section className="toy-edit">
                <button className="go-back-btn" onClick={this.onGoBack} title="go back">Back</button>
                <div className="toy-edit-form">
                    <h2>Edit Toy</h2>
                    <form
                        onSubmit={this.onSave}
                    >
                        <div>
                            <label htmlFor="text">Enter new name</label>
                            <input type="text" ref={this.inputRef} name="name" value={toy.name} onChange={this.handleChange} placeholder="Enter new name" required />
                        </div>
                        <div>
                            <label htmlFor="text">Enter new price</label>

                            <input type="number" name="price" value={toy.price} onChange={this.handleChange} placeholder="Enter new price" required />
                        </div>

                        <div>
                            <label>

                                <input onChange={this.handleChange} list="allOptions" name="inStock"
                                    // value={toy.inStock}
                                    placeholder="is the product in stock?" />
                            </label>
                            <datalist id="allOptions">
                                <option value="Yes" />
                                <option value="No" />

                            </datalist>
                        </div>

                        <button>Save Changes!</button>
                    </form>
                </div>
            </section>
        )
    }
}




function mapStateToProps() {
    return {

    }
}

export const ToyEdit = connect(mapStateToProps)(_ToyEdit)