import React from 'react'
// import { connect } from 'react-redux'
import { ToyPreview } from './toy-preview'

export function ToyList({ toys, onRemoveToy, onToggleToy, user }) {
    if (!toys) return <div><p>Loading...</p> <img alt='loader' src='https://raw.githubusercontent.com/SamHerbert/SVG-Loaders/5deed925369e57e9c58ba576ce303466984db501/svg-loaders/spinning-circles.svg' /></div>
    if (!toys.length) return <div><p>No Toys Yet!</p> <img alt='loader' src='https://raw.githubusercontent.com/SamHerbert/SVG-Loaders/5deed925369e57e9c58ba576ce303466984db501/svg-loaders/spinning-circles.svg' /></div>
    return toys.map((toy, idx) => {
        return <ToyPreview key={idx} user={user} toy={toy} onRemoveToy={onRemoveToy} onToggleToy={onToggleToy} />
    })
}

